.App {
  color: #fff;
  background: #141414;
  height: 1200px;
}
.film-css > .row {
  overflow-x: auto;
  flex-wrap: nowrap;
}

.image-container {
  position: relative;
  transition: transform 0.3s;
}

.image-container:hover {
  cursor: pointer;
  transform:  scale(1.1);
}

.overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.image-container:hover > .overlay {
  opacity: 1;
}
